.mint-popup {
  .modal-content {
    background: rgba(17, 25, 40, 0.95);
    border-radius: 16px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(12px);
    padding: 1rem;
  }

  .modal-body {
    padding: 2rem;
    text-align: center;
    color: white;
  }

  .mint-status {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
    min-height: 200px;
    
    h4 {
      font-size: 1.5rem;
      margin: 0;
      font-weight: 600;
    }

    p {
      color: rgba(255, 255, 255, 0.8);
      margin: 0;
    }

    &.approving, &.minting {
      .spinner-border {
        width: 3rem;
        height: 3rem;
        color: #4f46e5;
      }
    }

    &.success {
      .success-icon {
        color: #22c55e;
        animation: scaleIn 0.3s ease-out;
      }

      .token-info {
        background: rgba(34, 197, 94, 0.1);
        padding: 0.75rem 1.5rem;
        border-radius: 8px;
        margin-top: 1rem;
      }
    }

    &.error {
      .error-icon {
        color: #ef4444;
        animation: scaleIn 0.3s ease-out;
      }
    }
  }

  .tx-link {
    display: inline-block;
    color: #4f46e5;
    text-decoration: none;
    padding: 0.5rem 1rem;
    border-radius: 8px;
    background: rgba(79, 70, 229, 0.1);
    transition: all 0.2s ease;

    &:hover {
      background: rgba(79, 70, 229, 0.2);
    }
  }

  .close-button {
    margin-top: 1.5rem;
    padding: 0.75rem 2rem;
    border-radius: 8px;
    border: none;
    background: #4f46e5;
    color: white;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;

    &:hover {
      background: #4338ca;
    }
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.mint-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-out;
}

.mint-popup-container {
  width: 90%;
  max-width: 400px;
  margin: 20px;
  animation: slideUp 0.4s ease-out;
}

.mint-popup-content {
  background: linear-gradient(145deg, #1a1a1a, #2a2a2a);
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  color: white;
  text-align: center;
  position: relative;
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, #3b82f6, #8b5cf6);
  }

  .popup-logo {
    width: 120px;
    height: 120px;
    margin: -1rem auto 1rem;
    position: relative;
    z-index: 2;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.2));
      animation: logoFloat 3s ease-in-out infinite;
    }
  }

  h3 {
    font-size: 1.5rem;
    font-weight: 700;
    margin: 1.5rem 0 1rem;
    background: linear-gradient(90deg, #fff, #ccc);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
}

// Loading Animation
.loading-animation {
  display: flex;
  gap: 8px;
  justify-content: center;
  margin: 20px 0;

  .circle {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #3b82f6;
    animation: bounce 0.5s ease-in-out infinite;

    &:nth-child(2) {
      animation-delay: 0.1s;
      background: #6366f1;
    }

    &:nth-child(3) {
      animation-delay: 0.2s;
      background: #8b5cf6;
    }
  }
}

.status-message {
  color: #94a3b8;
  font-size: 0.95rem;
  margin: 0.5rem 0;
  line-height: 1.5;
}

// Success Animation
.success-animation {
  margin: 20px auto;
  width: 80px;
  height: 80px;
  position: relative;
}

.checkmark {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  display: block;
  stroke-width: 2;
  stroke: #4ade80;
  stroke-miterlimit: 10;
  box-shadow: inset 0px 0px 0px #4ade80;
  animation: fill .4s ease-in-out .4s forwards, scale .3s ease-in-out .9s both;
}

.checkmark-circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 2;
  stroke-miterlimit: 10;
  stroke: #4ade80;
  fill: none;
  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

.checkmark-check {
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}

// Success Details
.success-details {
  margin: 1.5rem 0;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.token-display {
  background: rgba(79, 70, 229, 0.1);
  border-radius: 12px;
  padding: 0.75rem;
  display: flex;
  justify-content: center;
  gap: 0.5rem;

  .label {
    color: #94a3b8;
  }

  .value {
    color: #fff;
    font-weight: 600;
  }
}

.etherscan-link {
  color: #3b82f6;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 500;
  transition: all 0.2s;

  .link-arrow {
    transition: transform 0.2s;
  }

  &:hover {
    color: #60a5fa;
    .link-arrow {
      transform: translateX(3px);
    }
  }
}

// Error State
.error-content {
  .error-icon {
    width: 60px;
    height: 60px;
    background: rgba(239, 68, 68, 0.1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px auto;
    border: 2px solid #ef4444;

    span {
      color: #ef4444;
      font-size: 2rem;
      font-weight: bold;
    }
  }

  .error-message {
    color: #94a3b8;
    margin: 1rem 0;
  }
}

// Button
.close-button {
  background: linear-gradient(90deg, #3b82f6, #8b5cf6);
  border: none;
  padding: 0.75rem 2rem;
  border-radius: 12px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  width: 100%;
  margin-top: 1rem;

  &:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(59, 130, 246, 0.2);
  }

  &:active {
    transform: translateY(0);
  }
}

// Animations
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes scale {
  0%, 100% {
    transform: none;
  }
  50% {
    transform: scale3d(1.1, 1.1, 1);
  }
}

@keyframes fill {
  100% {
    box-shadow: inset 0px 0px 0px 30px #4ade80;
  }
}

@keyframes logoFloat {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
} 