// Enhanced Professional Amount Input Styling
$primary-color: #36caba;
$secondary-color: #6f36ca;
$dark-bg: #0f0f1e;
$darker-bg: #0a0a14;
$gradient-primary: linear-gradient(135deg, $primary-color 0%, $secondary-color 100%);
$gradient-glass: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.05));

// Enhanced keyframes
@keyframes inputGlow {
  0%, 100% { box-shadow: 0 4px 15px rgba($primary-color, 0.3); }
  50% { box-shadow: 0 6px 25px rgba($primary-color, 0.5); }
}

@keyframes slideShine {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes pulseIcon {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

.amount-input-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
  
  .input-wrapper {
    display: flex;
    align-items: center;
    background: $gradient-glass;
    backdrop-filter: blur(15px);
    border: 2px solid rgba($primary-color, 0.3);
    border-radius: 15px;
    padding: 0.3rem;
    margin-bottom: 1rem;
    box-shadow: 
      0 8px 25px rgba(0, 0, 0, 0.3),
      inset 0 1px 0 rgba(255, 255, 255, 0.1);
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    position: relative;
    overflow: hidden;
    
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100%;
      background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
      transition: left 0.6s ease;
    }
    
    &:focus-within {
      border-color: rgba($primary-color, 0.8);
      animation: inputGlow 2s infinite;
      transform: translateY(-2px);
      
      &::before {
        left: 100%;
      }
      
      .currency-icon {
        animation: pulseIcon 1s infinite;
      }
    }
    
    &:hover {
      border-color: rgba($primary-color, 0.6);
      transform: translateY(-1px);
      box-shadow: 
        0 12px 35px rgba(0, 0, 0, 0.4),
        inset 0 1px 0 rgba(255, 255, 255, 0.15);
    }
    
    .currency-icon {
      display: flex;
      align-items: center;
      justify-content: center;
      background: $gradient-primary;
      color: white;
      font-family: 'TheBoldFont', sans-serif;
      font-size: 1rem;
      font-weight: bold;
      height: 40px;
      min-width: 55px;
      border-radius: 10px;
      margin-right: 0.8rem;
      box-shadow: 
        0 4px 15px rgba($primary-color, 0.4),
        inset 0 1px 0 rgba(255, 255, 255, 0.2);
      transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
      text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
      letter-spacing: 0.5px;
      position: relative;
      overflow: hidden;
      
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
        animation: slideShine 3s infinite;
      }
      
      &:hover {
        transform: scale(1.05);
        box-shadow: 
          0 6px 20px rgba($primary-color, 0.6),
          inset 0 1px 0 rgba(255, 255, 255, 0.3);
      }
    }
    
    .deposit-input {
      flex: 1;
      background: transparent;
      border: none;
      color: white;
      font-family: 'TheBoldFont', sans-serif;
      font-size: 1.2rem;
      font-weight: 600;
      padding: 0.8rem;
      outline: none;
      transition: all 0.3s ease;
      letter-spacing: 0.5px;
      
      &::placeholder {
        color: rgba(255, 255, 255, 0.5);
        font-weight: 400;
        letter-spacing: 0.3px;
      }
      
      &::-webkit-inner-spin-button,
      &::-webkit-outer-spin-button {
        -webkit-appearance: none;
        margin: 0;
      }
      
      &:focus {
        color: $primary-color;
        text-shadow: 0 0 10px rgba($primary-color, 0.5);
      }
    }
  }
  
  .amount-range {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0.8rem;
    padding: 0 0.5rem;
    
    .range-label {
      font-size: 0.9rem;
      color: rgba(255, 255, 255, 0.7);
      font-weight: 500;
      letter-spacing: 0.3px;
      
      span {
        color: $primary-color;
        font-weight: bold;
        text-shadow: 0 0 8px rgba($primary-color, 0.5);
      }
    }
    
    .range-slider {
      flex: 1;
      height: 8px;
      background: rgba($darker-bg, 0.8);
      border-radius: 6px;
      margin: 0 1rem;
      position: relative;
      overflow: hidden;
      border: 1px solid rgba($primary-color, 0.2);
      box-shadow: 
        inset 0 2px 4px rgba(0, 0, 0, 0.3),
        0 1px 2px rgba(255, 255, 255, 0.1);
      
      .slider-track {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.05);
      }
      
      .slider-progress {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        background: $gradient-primary;
        border-radius: 6px;
        transition: width 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
        position: relative;
        
        &::after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
          animation: slideShine 2s linear infinite;
        }
        
        &::before {
          content: '';
          position: absolute;
          top: -1px;
          left: -1px;
          right: -1px;
          bottom: -1px;
          background: $gradient-primary;
          border-radius: 6px;
          z-index: -1;
          filter: blur(4px);
          opacity: 0.7;
        }
      }
    }
  }
}

.deposit-amount {
  font-family: 'TheBoldFont', sans-serif;
  font-size: 1.4rem;
  color: $primary-color;
  font-weight: bold;
  text-shadow: 0 0 15px rgba($primary-color, 0.5);
  letter-spacing: 0.5px;
  transition: all 0.3s ease;
  
  &:hover {
    transform: scale(1.02);
    text-shadow: 0 0 20px rgba($primary-color, 0.8);
  }
  
  &::after {
    content: ' tokens';
    font-size: 1rem;
    color: rgba(255, 255, 255, 0.7);
    margin-left: 0.4rem;
    font-weight: 500;
    letter-spacing: 0.3px;
  }
}

// Enhanced responsive design
@media (max-width: 768px) {
  .amount-input-container {
    .input-wrapper {
      flex-direction: column;
      padding: 0.5rem;
      
      .currency-icon {
        width: 100%;
        margin-right: 0;
        margin-bottom: 0.5rem;
        min-width: auto;
        height: 35px;
      }
      
      .deposit-input {
        text-align: center;
        font-size: 1.1rem;
      }
    }
    
    .amount-range {
      flex-direction: column;
      gap: 0.5rem;
      
      .range-slider {
        margin: 0;
        width: 100%;
      }
    }
  }
  
  .deposit-amount {
    font-size: 1.2rem;
    text-align: center;
    
    &::after {
      font-size: 0.9rem;
    }
  }
}

// Loading state enhancement
.amount-input-container.loading {
  .input-wrapper {
    position: relative;
    
    &::before {
      animation: slideShine 1.5s infinite;
    }
    
    .currency-icon {
      animation: pulseIcon 1s infinite;
    }
  }
}

// Error state
.amount-input-container.error {
  .input-wrapper {
    border-color: rgba(255, 107, 107, 0.6);
    animation: inputGlow 2s infinite;
    
    &:focus-within {
      border-color: rgba(255, 107, 107, 0.8);
    }
  }
}

// Success state
.amount-input-container.success {
  .input-wrapper {
    border-color: rgba(76, 175, 80, 0.6);
    
    &:focus-within {
      border-color: rgba(76, 175, 80, 0.8);
    }
  }
}
