// Import the bold font
@font-face {
  font-family: 'TheBoldFont';
  src: url('../assets/fonts/THEBOLDFONT-FREEVERSION.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

// Import custom styles
@import '../styles/amount-input.scss';

// Variables
$primary-color: #36caba;
$secondary-color: #6f36ca;
$dark-bg: #0f0f1e;
$darker-bg: #0a0a14;
$card-bg: rgba(15, 15, 30, 0.9);
$border-color: rgba(54, 202, 186, 0.3);
$text-color: #ffffff;
$gradient-primary: linear-gradient(135deg, $primary-color 0%, $secondary-color 100%);

// Element colors
$fire-color: #ff5e3a;
$water-color: #4facfe;
$earth-color: #43a047;
$air-color: #90caf9;

// Rarity colors
$legendary-color: #ff9800;
$epic-color: #e040fb;
$rare-color: #3f51b5;
$uncommon-color: #4caf50;
$common-color: #9e9e9e;

// Keyframes for animations
@keyframes float {
  0% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
  100% { transform: translateY(0); }
}

@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.4); }
  70% { box-shadow: 0 0 0 15px rgba(255, 255, 255, 0); }
  100% { box-shadow: 0 0 0 0 rgba(255, 255, 255, 0); }
}

@keyframes glow {
  0% { filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.7)); }
  50% { filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.9)); }
  100% { filter: drop-shadow(0 0 5px rgba(255, 255, 255, 0.7)); }
}

@keyframes shimmer {
  0% { background-position: 0% 50%; }
  100% { background-position: 100% 50%; }
}

@keyframes rotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes btnShine {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

// Removed duplicate body background styling to use global background instead

.container-fluid {
  max-width: 1600px;
  margin: 0 auto;
}

// Hero Section
.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 40vh;
  text-align: center;
  position: relative;
  padding: 3rem 2rem;
  background: linear-gradient(135deg, rgba($dark-bg, 0.8) 0%, rgba($darker-bg, 0.9) 100%);
  border-radius: 20px;
  margin: 2rem 0;
  backdrop-filter: blur(10px);
  border: 1px solid $border-color;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: $gradient-primary;
    z-index: 1;
  }
  
  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: 
      radial-gradient(circle at 20% 30%, rgba($primary-color, 0.1) 0%, transparent 25%),
      radial-gradient(circle at 80% 70%, rgba($secondary-color, 0.1) 0%, transparent 25%);
    z-index: 0;
  }
  
  h1 {
    font-family: 'TheBoldFont', sans-serif;
    font-size: 4rem;
    margin-bottom: 1rem;
    background: linear-gradient(90deg, #ffffff, $primary-color, $secondary-color);
    background-size: 200% auto;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: shimmer 3s linear infinite;
    text-shadow: 0 0 20px rgba($primary-color, 0.5);
    position: relative;
    z-index: 2;
    
    span {
      color: $primary-color;
      filter: drop-shadow(0 0 10px rgba($primary-color, 0.8));
    }
    
    @media (max-width: 768px) {
      font-size: 2.5rem;
    }
  }
  
  .hero-subtitle {
    color: $text-color;
    font-size: 1.2rem;
    max-width: 600px;
    margin: 0 auto 2rem;
    opacity: 0.8;
    position: relative;
    z-index: 2;
    
    @media (max-width: 768px) {
      font-size: 1rem;
    }
  }
  
  &.mint-hero {
    min-height: 30vh;
  }
  
  &.connect-hero {
    min-height: 60vh;
    margin: 2rem;
  }
}

.hero-content {
  max-width: 800px;
  margin: 0 auto;
  
  .hero-title {
    font-family: 'TheBoldFont', sans-serif;
    font-size: 4rem;
    margin-bottom: 2rem;
    color: #ffffff;
    
    span {
      background: linear-gradient(90deg, #36caba, #6f36ca);
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    
    @media (max-width: 768px) {
      font-size: 2.5rem;
    }
  }
}

// Connect Section
.connect-section {
  margin-top: 3rem;
  display: flex;
  justify-content: center;
}

.custom-connect-btn {
  position: relative;
  background: linear-gradient(135deg, #36caba 0%, #6f36ca 100%);
  border: none;
  border-radius: 15px;
  padding: 1rem 2rem;
  color: white;
  font-weight: bold;
  font-size: 1.1rem;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 0 8px 32px rgba(54, 202, 186, 0.3);
  
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 12px 40px rgba(54, 202, 186, 0.4);
  }
  
  &:active {
    transform: translateY(0);
  }
  
  .btn-content {
    position: relative;
    z-index: 2;
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .btn-text {
    font-family: 'TheBoldFont', sans-serif;
  }
  
  .btn-shine {
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    animation: btnShine 2s infinite;
  }
}

// NFT Showcase
.nft-showcase-col {
  padding: 1rem;
  
  @media (max-width: 992px) {
    margin-bottom: 2rem;
  }
}

.nft-showcase {
  background: rgba($dark-bg, 0.7);
  border-radius: 25px;
  padding: 2.5rem;
  border: 1px solid rgba($primary-color, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.4);
  
  .showcase-title {
    font-family: 'TheBoldFont', sans-serif;
    font-size: 2.5rem;
    margin-bottom: 2.5rem;
    color: $text-color;
    text-align: center;
    position: relative;
    font-weight: 900;
    background: linear-gradient(90deg, #ffffff, $primary-color, $secondary-color);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: shimmer 3s linear infinite;
    
    &::after {
      content: '';
      position: absolute;
      bottom: -15px;
      left: 50%;
      transform: translateX(-50%);
      width: 100px;
      height: 3px;
      background: $gradient-primary;
      border-radius: 3px;
      box-shadow: 0 0 20px rgba($primary-color, 0.5);
    }
  }

  .nft-image-container {
    position: relative;
    padding-top: 100%;
    overflow: hidden;
    
    .nft-image {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: transform 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    }
    
    .nft-overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.1) 50%, transparent 70%);
      opacity: 0;
      transition: opacity 0.3s ease;
    }
    
    .nft-power-aura {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 120%;
      height: 120%;
      transform: translate(-50%, -50%) scale(0.9);
      opacity: 0;
      transition: all 0.5s ease;
      filter: blur(20px);
    }
    
    .element-badge {
      position: absolute;
      top: 15px;
      right: 15px;
      padding: 0.5rem 1rem;
      border-radius: 20px;
      font-size: 0.9rem;
      font-weight: bold;
      color: white;
      z-index: 2;
      display: flex;
      align-items: center;
      gap: 0.5rem;
      backdrop-filter: blur(5px);
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
      
      &.fire {
        background: linear-gradient(135deg, $fire-color, lighten($fire-color, 10%));
      }
      
      &.water {
        background: linear-gradient(135deg, $water-color, lighten($water-color, 10%));
      }
      
      &.earth {
        background: linear-gradient(135deg, $earth-color, lighten($earth-color, 10%));
      }
      
      &.air {
        background: linear-gradient(135deg, $air-color, lighten($air-color, 10%));
      }
      
      .element-icon {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: rgba(255, 255, 255, 0.3);
      }
    }
  }

  .nft-info {
    padding: 1.5rem;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.4), transparent);
    
    h3 {
      font-size: 1.1rem;
      margin: 0 0 1rem;
      color: $text-color;
      font-weight: bold;
      text-align: center;
      text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
    }
  }

  .nft-stats {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    align-items: center;
  }

  .power-level {
    width: 100%;
    
    .power-bar {
      height: 6px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 3px;
      overflow: hidden;
      
      .power-fill {
        width: 0;
        height: 100%;
        transition: width 0.8s cubic-bezier(0.175, 0.885, 0.32, 1.275);
      }
    }
  }

  .rarity-badge {
    display: inline-block;
    padding: 0.4rem 1rem;
    border-radius: 15px;
    font-size: 0.8rem;
    font-weight: bold;
    color: white;
    text-transform: uppercase;
    letter-spacing: 1px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    
    &.legendary {
      background: linear-gradient(135deg, $legendary-color, lighten($legendary-color, 10%));
    }
    
    &.epic {
      background: linear-gradient(135deg, $epic-color, lighten($epic-color, 10%));
    }
    
    &.rare {
      background: linear-gradient(135deg, $rare-color, lighten($rare-color, 10%));
    }
    
    &.uncommon {
      background: linear-gradient(135deg, $uncommon-color, lighten($uncommon-color, 10%));
    }
    
    &.common {
      background: linear-gradient(135deg, $common-color, lighten($common-color, 10%));
    }
  }
}

.nft-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 2rem;
  padding: 1rem;
  
  @media (max-width: 768px) {
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: 1.5rem;
  }
}

.nft-card {
  position: relative;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  
  &.fire-card {
    .card-inner { border-color: rgba($fire-color, 0.5); }
    .power-fill { background: linear-gradient(90deg, $fire-color, lighten($fire-color, 15%)); }
    .nft-power-aura { background: radial-gradient(circle, rgba($fire-color, 0.3) 0%, transparent 70%); }
  }
  
  &.water-card {
    .card-inner { border-color: rgba($water-color, 0.5); }
    .power-fill { background: linear-gradient(90deg, $water-color, lighten($water-color, 15%)); }
    .nft-power-aura { background: radial-gradient(circle, rgba($water-color, 0.3) 0%, transparent 70%); }
  }
  
  &.earth-card {
    .card-inner { border-color: rgba($earth-color, 0.5); }
    .power-fill { background: linear-gradient(90deg, $earth-color, lighten($earth-color, 15%)); }
    .nft-power-aura { background: radial-gradient(circle, rgba($earth-color, 0.3) 0%, transparent 70%); }
  }
  
  &.air-card {
    .card-inner { border-color: rgba($air-color, 0.5); }
    .power-fill { background: linear-gradient(90deg, $air-color, lighten($air-color, 15%)); }
    .nft-power-aura { background: radial-gradient(circle, rgba($air-color, 0.3) 0%, transparent 70%); }
  }
  
  &:hover {
    transform: translateY(-10px);
    
    .card-inner {
      box-shadow: 
        0 20px 40px rgba(0, 0, 0, 0.4),
        0 0 30px rgba($primary-color, 0.3);
    }
    
    
    .nft-power-aura {
      opacity: 1;
      transform: scale(1.2);
    }
    
    .power-fill {
      width: 100%;
      transition-delay: 0.2s;
    }
  }
}

.card-inner {
  background: rgba($darker-bg, 0.95);
  border-radius: 20px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  overflow: hidden;
  backdrop-filter: blur(10px);
  box-shadow: 
    0 10px 20px rgba(0, 0, 0, 0.3),
    inset 0 0 0 1px rgba(255, 255, 255, 0.1);
  transition: all 0.4s ease;
}

.nft-info {
  padding: 1.5rem;
  text-align: center;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.4), transparent);
  
  h4 {
    color: white;
    margin: 0 0 0.5rem;
    font-size: 1.2rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  p {
    color: rgba(255, 255, 255, 0.7);
    margin: 0;
    font-size: 0.9rem;
    font-style: italic;
  }
}

.element-footer {
  padding: 1.5rem;
  
  .power-bar {
    height: 6px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 3px;
    overflow: hidden;
    
    .power-fill {
      width: 0;
      height: 100%;
      transition: width 0.8s cubic-bezier(0.4, 0, 0.2, 1);
    }
  }
}

// Responsive adjustments
@media (max-width: 768px) {
  .nft-showcase {
    padding: 1.5rem;
    
    .showcase-title {
      font-size: 2rem;
      margin-bottom: 2rem;
    }
  }
  
  .nft-card {
    .nft-info {
      padding: 1rem;
      
      h3 {
        font-size: 1rem;
      }
    }
    
    .element-badge {
      padding: 0.4rem 0.8rem;
      font-size: 0.8rem;
    }
  }
}

// Mint Banner Container (similar to Mission.jsx)
.mint-banner-container {
  margin: 2rem 0 3rem;
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1rem;
  
  .mint-banner-wrapper {
    position: relative;
    width: 100%;
    border-radius: 25px;
    overflow: hidden;
    border: 2px solid rgba($primary-color, 0.3);
    box-shadow: 
      0 20px 40px rgba(0, 0, 0, 0.4),
      0 0 60px rgba($primary-color, 0.2);
    backdrop-filter: blur(20px);
    transition: all 0.3s ease;
    
    &:hover {
      border-color: rgba($primary-color, 0.5);
      box-shadow: 
        0 25px 50px rgba(0, 0, 0, 0.5),
        0 0 80px rgba($primary-color, 0.3);
    }
    
    .mint-banner-image {
      position: relative;
      width: 100%;
      height: 280px;
      overflow: hidden;
      
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.6s ease;
      }
      
      &:hover img {
        transform: scale(1.05);
      }
      
      .mint-overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(
          135deg,
          rgba($dark-bg, 0.7) 0%,
          rgba($darker-bg, 0.8) 50%,
          rgba($primary-color, 0.2) 100%
        );
        z-index: 1;
      }
      
      .mint-badge {
        position: absolute;
        top: 20px;
        left: 20px;
        background: $gradient-primary;
        color: white;
        padding: 0.5rem 1.5rem;
        border-radius: 50px;
        font-size: 0.9rem;
        font-weight: bold;
        text-transform: uppercase;
        letter-spacing: 1px;
        z-index: 2;
        box-shadow: 0 5px 20px rgba($primary-color, 0.4);
        border: 1px solid rgba(255, 255, 255, 0.2);
        backdrop-filter: blur(10px);
        
        &::before {
          content: '';
          position: absolute;
          top: -1px;
          left: -1px;
          right: -1px;
          bottom: -1px;
          background: $gradient-primary;
          border-radius: 50px;
          z-index: -1;
          filter: blur(3px);
          opacity: 0.6;
        }
      }
    }
    
    .mint-banner-content {
      padding: 1.5rem;
      position: relative;
      z-index: 2;
      background: linear-gradient(
        to bottom,
        rgba($darker-bg, 0.95) 0%,
        rgba($dark-bg, 0.9) 100%
      );
      
      .mint-title {
        font-family: 'TheBoldFont', sans-serif;
        font-size: 3rem;
        margin: 0 0 1rem 0;
        color: #ffffff;
        text-shadow: 0 0 20px rgba($primary-color, 0.5);
        font-weight: 900;
        text-transform: uppercase;
        letter-spacing: 2px;
        
        @media (max-width: 768px) {
          font-size: 2rem;
        }
      }
      
      .mint-level {
        margin-bottom: 1.5rem;
        
        .level-indicator {
          display: flex;
          gap: 0.5rem;
          
          .level-star {
            color: $primary-color;
            font-size: 1.5rem;
            text-shadow: 0 0 10px rgba($primary-color, 0.8);
            animation: glow 2s ease-in-out infinite alternate;
            
            &:nth-child(2) { animation-delay: 0.5s; }
            &:nth-child(3) { animation-delay: 1s; }
            &:nth-child(4) { animation-delay: 1.5s; }
          }
        }
      }
      
      .mint-description {
        color: rgba($text-color, 0.9);
        font-size: 1.1rem;
        line-height: 1.7;
        margin-bottom: 2rem;
        text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
        
        @media (max-width: 768px) {
          font-size: 1rem;
        }
      }
      
      .mint-features {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
        gap: 1.5rem;
        
        @media (max-width: 768px) {
          grid-template-columns: repeat(2, 1fr);
          gap: 1rem;
        }
        
        .feature-item {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 0.5rem;
          padding: 1rem;
          background: rgba($darker-bg, 0.6);
          border-radius: 15px;
          border: 1px solid rgba($primary-color, 0.2);
          transition: all 0.3s ease;
          backdrop-filter: blur(10px);
          
          &:hover {
            transform: translateY(-5px);
            border-color: rgba($primary-color, 0.5);
            box-shadow: 0 10px 30px rgba($primary-color, 0.2);
          }
          
          .feature-icon {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
              filter: brightness(1.2);
            }
          }
          
          .feature-label {
            color: $text-color;
            font-size: 0.8rem;
            font-weight: bold;
            text-transform: uppercase;
            letter-spacing: 1px;
            text-align: center;
            
            @media (max-width: 768px) {
              font-size: 0.7rem;
            }
          }
        }
      }
      
      // Connect Section within banner content
      .connect-section {
        margin-top: 2rem;
        display: flex;
        justify-content: center;
        
        .custom-connect-btn {
          position: relative;
          background: linear-gradient(135deg, #36caba 0%, #6f36ca 100%);
          border: none;
          border-radius: 15px;
          padding: 1rem 2rem;
          color: white;
          font-weight: bold;
          font-size: 1.1rem;
          cursor: pointer;
          overflow: hidden;
          transition: all 0.3s ease;
          box-shadow: 0 8px 32px rgba(54, 202, 186, 0.3);
          
          &:hover {
            transform: translateY(-2px);
            box-shadow: 0 12px 40px rgba(54, 202, 186, 0.4);
          }
          
          &:active {
            transform: translateY(0);
          }
          
          .btn-content {
            position: relative;
            z-index: 2;
            display: flex;
            align-items: center;
            gap: 0.5rem;
          }
          
          .btn-text {
            font-family: 'TheBoldFont', sans-serif;
          }
          
          .btn-shine {
            position: absolute;
            top: 0;
            left: -100%;
            width: 100%;
            height: 100%;
            background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
            animation: btnShine 2s infinite;
          }
        }
      }
    }
  }
  
  // Integrated Mint Section within Banner
  .integrated-mint-section {
    padding: 2rem;
    border-top: 1px solid rgba($primary-color, 0.2);
    background: linear-gradient(
      to bottom,
      rgba($darker-bg, 0.95) 0%,
      rgba($dark-bg, 0.9) 100%
    );
    
    .row {
      margin: 0;
    }
    
    // Preview section for non-connected state
    .preview-section {
      text-align: center;
      
      .preview-title {
        font-family: 'TheBoldFont', sans-serif;
        font-size: 2rem;
        color: white;
        margin-bottom: 2rem;
        text-transform: uppercase;
        letter-spacing: 1px;
        
        @media (max-width: 768px) {
          font-size: 1.5rem;
          margin-bottom: 1.5rem;
        }
      }
      
      .nft-preview-grid {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
        gap: 1.5rem;
        max-width: 600px;
        margin: 0 auto;
        
        @media (max-width: 768px) {
          grid-template-columns: repeat(3, 1fr);
          gap: 1rem;
        }
        
        .preview-card {
          background: rgba($darker-bg, 0.7);
          border-radius: 15px;
          overflow: hidden;
          border: 2px solid rgba($primary-color, 0.3);
          transition: all 0.3s ease;
          aspect-ratio: 1;
          
          &:hover {
            transform: translateY(-5px);
            border-color: rgba($primary-color, 0.6);
            box-shadow: 0 10px 30px rgba($primary-color, 0.3);
          }
          
          .preview-image {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform 0.3s ease;
          }
          
          &:hover .preview-image {
            transform: scale(1.1);
          }
        }
      }
    }
  }
}

// Mission Earn Container
.mission-earn-container {
  margin: 3rem 0;
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1rem;
  
  .mission-earn-wrapper {
    position: relative;
    width: 100%;
    background: rgba($darker-bg, 0.95);
    border-radius: 25px;
    overflow: hidden;
    border: 2px solid rgba(#ffd700, 0.3); // Gold accent for earning theme
    box-shadow: 
      0 20px 40px rgba(0, 0, 0, 0.4),
      0 0 60px rgba(#ffd700, 0.2);
    backdrop-filter: blur(20px);
    transition: all 0.3s ease;
    
    &:hover {
      border-color: rgba(#ffd700, 0.5);
      box-shadow: 
        0 25px 50px rgba(0, 0, 0, 0.5),
        0 0 80px rgba(#ffd700, 0.3);
    }
    
    .mission-earn-image {
      position: relative;
      width: 100%;
      height: 250px;
      overflow: hidden;
      
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.6s ease;
        filter: sepia(0.2) saturate(1.2) contrast(1.1); // Cool hidden effect
      }
      
      &:hover img {
        transform: scale(1.05);
        filter: sepia(0.3) saturate(1.4) contrast(1.2);
      }
      
      .mission-overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(
          135deg,
          rgba($dark-bg, 0.6) 0%,
          rgba(#ffd700, 0.1) 30%,
          rgba($darker-bg, 0.7) 70%,
          rgba(#ff6b35, 0.2) 100%
        );
        z-index: 1;
      }
      
      .mission-badge {
        position: absolute;
        top: 20px;
        right: 20px;
        background: linear-gradient(135deg, #ffd700, #ff8c00);
        color: #000;
        padding: 0.5rem 1.5rem;
        border-radius: 50px;
        font-size: 0.9rem;
        font-weight: bold;
        text-transform: uppercase;
        letter-spacing: 1px;
        z-index: 2;
        box-shadow: 0 5px 20px rgba(#ffd700, 0.4);
        border: 1px solid rgba(255, 255, 255, 0.2);
        backdrop-filter: blur(10px);
        
        &::before {
          content: '';
          position: absolute;
          top: -1px;
          left: -1px;
          right: -1px;
          bottom: -1px;
          background: linear-gradient(135deg, #ffd700, #ff8c00);
          border-radius: 50px;
          z-index: -1;
          filter: blur(3px);
          opacity: 0.6;
        }
      }
    }
    
    .mission-earn-content {
      padding: 2rem;
      position: relative;
      z-index: 2;
      background: linear-gradient(
        to bottom,
        rgba($darker-bg, 0.95) 0%,
        rgba($dark-bg, 0.9) 100%
      );
      
      .mission-earn-title {
        font-family: 'TheBoldFont', sans-serif;
        font-size: 2.5rem;
        margin: 0 0 1rem 0;
        background: linear-gradient(90deg, #ffd700, #ff8c00, #ffd700);
        background-size: 200% auto;
        -webkit-background-clip: text;
        background-clip: text;
        -webkit-text-fill-color: transparent;
        animation: shimmer 3s linear infinite;
        text-shadow: 0 0 20px rgba(#ffd700, 0.5);
        font-weight: 900;
        text-transform: uppercase;
        letter-spacing: 2px;
        text-align: center;
        
        @media (max-width: 768px) {
          font-size: 1.8rem;
        }
      }
      
      .mission-earn-description {
        color: rgba($text-color, 0.9);
        font-size: 1.1rem;
        line-height: 1.7;
        margin-bottom: 2rem;
        text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
        text-align: center;
        max-width: 600px;
        margin-left: auto;
        margin-right: auto;
        
        @media (max-width: 768px) {
          font-size: 1rem;
          margin-bottom: 1.5rem;
        }
      }
      
      .mission-features {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
        gap: 1.5rem;
        margin-bottom: 2rem;
        
        @media (max-width: 768px) {
          grid-template-columns: repeat(2, 1fr);
          gap: 1rem;
        }
        
        .mission-feature-item {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 0.5rem;
          padding: 1.5rem 1rem;
          background: rgba($darker-bg, 0.6);
          border-radius: 15px;
          border: 1px solid rgba(#ffd700, 0.2);
          transition: all 0.3s ease;
          backdrop-filter: blur(10px);
          
          &:hover {
            transform: translateY(-5px);
            border-color: rgba(#ffd700, 0.5);
            box-shadow: 0 10px 30px rgba(#ffd700, 0.2);
          }
          
          .mission-feature-icon {
            font-size: 2rem;
            margin-bottom: 0.5rem;
            filter: drop-shadow(0 0 10px rgba(#ffd700, 0.5));
          }
          
          .mission-feature-label {
            color: $text-color;
            font-size: 0.8rem;
            font-weight: bold;
            text-transform: uppercase;
            letter-spacing: 1px;
            text-align: center;
            
            @media (max-width: 768px) {
              font-size: 0.7rem;
            }
          }
        }
      }
      
      .mission-cta {
        display: flex;
        justify-content: center;
        
        .mission-cta-btn {
          position: relative;
          background: linear-gradient(135deg, #ffd700 0%, #ff8c00 100%);
          border: none;
          border-radius: 15px;
          padding: 1rem 2.5rem;
          color: #000;
          font-weight: bold;
          font-size: 1.1rem;
          cursor: pointer;
          overflow: hidden;
          transition: all 0.3s ease;
          box-shadow: 0 8px 32px rgba(#ffd700, 0.3);
          text-transform: uppercase;
          letter-spacing: 1px;
          font-family: 'TheBoldFont', sans-serif;
          
          &:hover {
            transform: translateY(-2px);
            box-shadow: 0 12px 40px rgba(#ffd700, 0.5);
          }
          
          &:active {
            transform: translateY(0);
          }
          
          .btn-text {
            position: relative;
            z-index: 2;
          }
          
          .btn-shine {
            position: absolute;
            top: 0;
            left: -100%;
            width: 100%;
            height: 100%;
            background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
            animation: btnShine 2s infinite;
            z-index: 1;
          }
        }
      }
    }
  }
}

// Responsive adjustments for mission container
@media (max-width: 768px) {
  .mission-earn-container {
    margin: 2rem 0;
    
    .mission-earn-wrapper {
      .mission-earn-image {
        height: 200px;
        
        .mission-badge {
          top: 15px;
          right: 15px;
          font-size: 0.8rem;
          padding: 0.4rem 1rem;
        }
      }
      
      .mission-earn-content {
        padding: 1.5rem;
        
        .mission-earn-title {
          font-size: 1.8rem;
          margin-bottom: 1rem;
        }
        
        .mission-earn-description {
          font-size: 1rem;
          margin-bottom: 1.5rem;
        }
        
        .mission-features {
          grid-template-columns: repeat(2, 1fr);
          gap: 1rem;
          margin-bottom: 1.5rem;
          
          .mission-feature-item {
            padding: 1rem 0.5rem;
            
            .mission-feature-icon {
              font-size: 1.5rem;
            }
            
            .mission-feature-label {
              font-size: 0.7rem;
            }
          }
        }
        
        .mission-cta {
          .mission-cta-btn {
            padding: 0.8rem 2rem;
            font-size: 1rem;
          }
        }
      }
    }
  }
}

// Balance and Mint specific styles - scoped to avoid conflicts
.balance-card {
  background: rgba(15, 15, 30, 0.9);
  border-radius: 20px;
  border: 1px solid rgba(54, 202, 186, 0.3);
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease;
  
  &:hover {
    border-color: rgba(54, 202, 186, 0.5);
    box-shadow: 0 12px 40px rgba(54, 202, 186, 0.2);
  }
  
  &.main-balance {
    min-width: 400px;
    max-width: 500px;
    
    @media (max-width: 768px) {
      min-width: 300px;
      max-width: 350px;
    }
  }
  
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, #36caba, #6f36ca);
  }
  
  .card-inner {
    padding: 2rem;
  }
}

.balance-content {
  color: white;
  font-family: 'Arial', sans-serif;
  
  > div {
    margin-bottom: 1rem;
    padding: 0.5rem 0;
    font-size: 1.1rem;
    
    &:not(:last-child) {
      border-bottom: 1px solid rgba(54, 202, 186, 0.2);
    }
    
    &:last-child {
      margin-top: 2rem;
      margin-bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 1rem;
      
      @media (max-width: 768px) {
        flex-direction: column;
        gap: 1rem;
      }
    }
  }
  
  // Input controls
  button {
    background: linear-gradient(135deg, #36caba, #6f36ca);
    border: none;
    border-radius: 8px;
    color: white;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 1.2rem;
    font-weight: bold;
    
    &:hover:not(:disabled) {
      transform: scale(1.1);
      box-shadow: 0 4px 15px rgba(54, 202, 186, 0.4);
    }
    
    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
    
    &:last-child {
      width: auto;
      height: auto;
      padding: 0.8rem 2rem;
      border-radius: 12px;
      font-size: 1rem;
      margin-top: 1rem;
      
      @media (max-width: 768px) {
        width: 100%;
      }
    }
  }
  
  input {
    background: rgba(26, 26, 46, 0.8);
    border: 1px solid rgba(54, 202, 186, 0.3);
    border-radius: 8px;
    color: white;
    text-align: center;
    width: 80px;
    height: 40px;
    font-size: 1.1rem;
    font-weight: bold;
    
    &:focus {
      outline: none;
      border-color: #36caba;
      box-shadow: 0 0 10px rgba(54, 202, 186, 0.3);
    }
  }
}

// Enhanced Mint Card (banner-themed)
.mint-card {
  position: relative;
  width: 100%;
  background: rgba($darker-bg, 0.95);
  border-radius: 25px;
  overflow: hidden;
  border: 2px solid rgba($primary-color, 0.3);
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.4),
    0 0 60px rgba($primary-color, 0.2);
  backdrop-filter: blur(20px);
  transition: all 0.3s ease;
  
  &:hover {
    border-color: rgba($primary-color, 0.5);
    box-shadow: 
      0 25px 50px rgba(0, 0, 0, 0.5),
      0 0 80px rgba($primary-color, 0.3);
  }
  
  &::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, 
      rgba($fire-color, 0.05),
      rgba($water-color, 0.05),
      rgba($earth-color, 0.05),
      rgba($air-color, 0.05)
    );
    opacity: 0.7;
    z-index: 0;
  }
  
  .card-body {
    padding: 2.5rem;
    position: relative;
    z-index: 2;
    background: linear-gradient(
      to bottom,
      rgba($darker-bg, 0.95) 0%,
      rgba($dark-bg, 0.9) 100%
    );
  }
}

// Enhanced Mint Progress
.mint-progress {
  margin-bottom: 2.5rem;
  
  .progress-text {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
    font-weight: bold;
    color: $text-color;
    font-size: 1.1rem;
    
    .mint-count {
      color: $primary-color;
      text-shadow: 0 0 10px rgba($primary-color, 0.5);
    }
  }
  
  .custom-progress {
    height: 12px;
    border-radius: 10px;
    background: rgba($darker-bg, 0.8);
    border: 1px solid rgba($primary-color, 0.3);
    overflow: hidden;
    position: relative;
    
    .progress-bar {
      height: 100%;
      background: $gradient-primary;
      border-radius: 10px;
      transition: width 1s cubic-bezier(0.4, 0, 0.2, 1);
      position: relative;
      
      .progress-glow {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
        animation: shimmer 2s linear infinite;
      }
    }
  }
}

// Enhanced Mint Info
.mint-info {
  margin-bottom: 2.5rem;
  
  .info-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 0;
    border-bottom: 1px solid rgba($primary-color, 0.2);
    
    &:last-child {
      border-bottom: none;
    }
    
    .info-label {
      display: flex;
      align-items: center;
      gap: 0.5rem;
      color: rgba($text-color, 0.8);
      font-weight: 500;
      font-size: 1rem;
      
      .gg-token {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background: $gradient-primary;
        box-shadow: 0 0 10px rgba($primary-color, 0.5);
      }
    }
    
    .info-value {
      color: $primary-color;
      font-weight: bold;
      font-size: 1.1rem;
      text-shadow: 0 0 10px rgba($primary-color, 0.5);
    }
  }
}

// Enhanced Mint Controls
.mint-controls {
  .amount-control {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 2rem;
    
    .amount-btn {
      background: $gradient-primary;
      border: none;
      border-radius: 12px;
      color: white;
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
      font-size: 1.5rem;
      font-weight: bold;
      box-shadow: 0 4px 15px rgba($primary-color, 0.3);
      
      &:hover:not(:disabled) {
        transform: scale(1.1) translateY(-2px);
        box-shadow: 0 8px 25px rgba($primary-color, 0.5);
      }
      
      &:disabled {
        opacity: 0.5;
        cursor: not-allowed;
        transform: none;
      }
      
      span {
        line-height: 1;
      }
      
      &.decrease {
        background: linear-gradient(135deg, rgba($fire-color, 0.8), rgba($fire-color, 1));
      }
      
      &.increase {
        background: linear-gradient(135deg, rgba($earth-color, 0.8), rgba($earth-color, 1));
      }
    }
    
    .amount-input-container {
      display: flex;
      align-items: center;
      justify-content: center;
      
      .amount-input {
        background: rgba($darker-bg, 0.9);
        border: 2px solid rgba($primary-color, 0.4);
        border-radius: 12px;
        color: white;
        text-align: center;
        width: 100px;
        height: 50px;
        font-size: 1.4rem;
        font-weight: bold;
        transition: all 0.3s ease;
        box-shadow: 
          0 4px 15px rgba(0, 0, 0, 0.3),
          inset 0 2px 5px rgba(0, 0, 0, 0.2);
        
        &:focus {
          outline: none;
          border-color: $primary-color;
          box-shadow: 
            0 0 20px rgba($primary-color, 0.4),
            inset 0 2px 5px rgba(0, 0, 0, 0.2);
        }
      }
    }
  }
  
  .mint-button {
    width: 100%;
    background: $gradient-primary;
    border: none;
    border-radius: 15px;
    padding: 1.2rem;
    color: white;
    font-weight: bold;
    font-size: 1.2rem;
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    box-shadow: 0 8px 30px rgba($primary-color, 0.4);
    text-transform: uppercase;
    letter-spacing: 1px;
    font-family: 'TheBoldFont', sans-serif;
    position: relative;
    overflow: hidden;
    
    .button-text {
      position: relative;
      z-index: 2;
    }
    
    .button-glow {
      position: absolute;
      top: 0;
      left: -100%;
      width: 100%;
      height: 100%;
      background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
      animation: btnShine 3s infinite;
      z-index: 1;
    }
    
    &:hover:not(:disabled) {
      transform: translateY(-3px);
      box-shadow: 0 15px 40px rgba($primary-color, 0.6);
    }
    
    &:disabled {
      opacity: 0.7;
      cursor: not-allowed;
      transform: none;
      
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 20px;
        height: 20px;
        margin: -10px 0 0 -10px;
        border: 2px solid rgba(255, 255, 255, 0.3);
        border-top: 2px solid white;
        border-radius: 50%;
        animation: rotate 1s linear infinite;
      }
    }
  }
}
